// Creating own less file because directly in tsx it's not possible: https://github.com/webpack-contrib/mini-css-extract-plugin/issues/73
@import "~antd/lib/style/themes/default";
@import "~antd/lib/style/core/motion";
@import "~antd/lib/spin/style/index";
@import "~antd/lib/message/style/index";
@import "~antd/lib/list/style/index";
@import "~antd/lib/modal/style/index";

// Fix border sizing for all available elements
[class*="real-queue-antd-"] {
    *,
    *::before,
    *::after {
        box-sizing: border-box;
    }
}

.real-queue-antd-list-item-meta-content {
    width: auto !important;
}

.real-queue-antd-list-item-meta-title {
    margin: 0 0 4px 0;
}

// Fix select dropdowns and messages in modals
.real-queue-antd-select-dropdown,
.real-queue-antd-message,
.real-queue-antd-image-preview-mask,
.real-queue-antd-image-preview-wrap {
    z-index: 105000000;
}

// Fix modal, popver and tooltip
.real-queue-antd-modal-mask,
.real-queue-antd-modal-wrap {
    z-index: 99999999;
}

// Fix modal height and body overflow
.real-queue-antd-modal-body {
    max-height: calc(100vh - 55px - 200px);
    overflow-y: auto;
}

@ant-prefix: real-queue-antd;